.get {
  margin-top: 30px;
  position: relative;
  &__wrapper {
    border-radius: 5px;
    box-shadow: 0 6px 49px rgba(0, 0, 0, 0.05);
    background-color: $white;
    padding-right: 37px;
    padding-bottom: 17px;
    box-sizing: border-box;
    @include respond-to(1131) {
      flex-direction: column;
      justify-content: center;
      align-items: center; } }
  &__img {
    max-width: 349px;
    width: 100%;
    position: relative;
    img {
      width: 100%;
      position: absolute;
      left: 5px;
      top: -20px;
      @include respond-to(1131) {
        position: relative;
        left: 0;
        top: -50px; } } }
  &__info {
    width: 65%;
    @include respond-to(781) {
      width: 100%;
      padding: 0 15px; } }
  &__text {
    font-family: $museo;
    font-size: 16px;
    color: $grey;
    display: inline-block;
    margin-top: 10px;
    @include respond-to(690) {
      font-size: 4.2vw; }
    @include respond-to(360) {
      font-size: 16px;
      margin: 40px 0; } }
  &__bottom {
    margin-top: 15px; }
  &__btn {
    float: right;
    @include respond-to(1131) {
      float: none;
      position: relative;
      left: 50%;
      transform: translateX(-50%); } }
  &__arrow {
    float: left;
    margin-top: 9px;
    @include respond-to(1179) {
      display: none; } } }

