body input:focus:required:invalid,
body textarea:focus:required:invalid {
  outline: none; }
body input:required:valid,
body textarea:required:valid {
  outline: none; }
a:active:focus {
  outline: none; }

html, body {
  height: 100%; }

body {
  font-size: 10px;
  position: relative;
  line-height: 1.2;
  font-family: $museo;
  background: $white;
  color: $grey4;
  opacity: 1;
  -webkit-font-smoothing: antialiased;
  -webkit-appearance: none;
  @include respond-to(1168) {
    font-size: 8px; } }

.main-wrapper {
  width: 100%;
  overflow: hidden; }

.clear {
  clear: both; }

.container {
  max-width: 1200px;
  width: 100%;
  padding: 0 40px;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
  @include respond-to(992) {
    padding: 0 20px; } }

.row {
  display: flex;
  flex-wrap: wrap; }

.section {
  width: 100%;
  position: relative;
  box-sizing: border-box; }

.space-between {
  justify-content: space-between; }

.space-around {
  justify-content: space-around; }

h1 {
  color: $black2;
  font-family: $museoBold;
  font-size: 3.6em;
  @include respond-to(992) {
    font-size: 5.84vw;
    text-align: center; }
  @include respond-to(690) {
    font-size: 5.70vw; }
  @include respond-to(556) {
    font-size: 5.6vw; }
  @include respond-to(480) {
    display: none; } }

.front-mob {
  display: none;
  @include respond-to(480) {
    display: block;
    font-size: 24px; }
  @include respond-to(396) {
    font-size: 22px; }
  @include respond-to(350) {
    font-size: 19px; } }

h2 {
  color: $green;
  font-family: $museoBold;
  font-size: 3.2em;
  @include respond-to(992) {
    font-size: 5.6vw; }
  @include respond-to(690) {
    font-size: 5.8vw; } }

h3 {
  color: $green;
  font-family: $museoBold;
  font-size: 25px;
  @include respond-to(690) {
    font-size: 5.4vw; }
  @include respond-to(360) {
    font-size: 20px; } }

ul {
  list-style-type: none; }

a {
  text-decoration: none; }

.text-center {
  text-align: center; }

.text-left {
  text-align: left; }

.epsilon {
  font-family: $epsilon; }

.btn {
  display: inline-block;
  width: 250px;
  height: 60px;
  box-shadow: inset 0 -3px 0 rgba(22, 22, 22, 0.17);
  border-radius: 5px;
  background-color: $blue;
  font-family: $museoMid;
  font-size: 18px;
  color: $white;
  background-image: linear-gradient(-202deg, #ffad09 0%, #ff8609 100%);
  transition: .5s ease-in-out;
  border: none;
  cursor: pointer;
  &:hover {
    transition: .5s ease-in-out;
    background-image: linear-gradient(-202deg, #ff8609 0%, #ffad09 100%); }
  @include respond-to(992) {
    width: 380px;
    height: 90px;
    font-size: 27px; }
  @include respond-to(480) {
    width: 250px;
    height: 60px;
    font-size: 18px; } }
.text {
  font-size: 1.6rem; }

.btn-cat {
  display: inline-block;
  width: 250px;
  height: 60px;
  box-shadow: inset 0 -3px 0 rgba(22, 22, 22, 0.17);
  border-radius: 5px;
  background-color: $blue;
  font-family: $museoMid;
  font-size: 18px;
  color: $white;
  background-image: linear-gradient(-202deg, #ffad09 0%, #ff8609 100%);
  transition: .5s ease-in-out;
  border: none;
  cursor: pointer;
  &:hover {
    transition: .5s ease-in-out;
    background-image: linear-gradient(-202deg, #ff8609 0%, #ffad09 100%); } }

.vk {
  display: inline-block;
  width: 34px;
  height: 34px;
  background-image: linear-gradient(to top, #3d598d 0%, #6789b9 100%);
  border-radius: 50%;
  position: relative;
  margin-left: 18px;
  @include respond-to(992) {
    width: 62px;
    height: 62px;
    margin-left: 0; }
  @include respond-to(480) {
    width: 34px;
    height: 34px; }
  &:after {
    content: "";
    width: 50px;
    height: 50px;
    background-image: linear-gradient(to top, #3d598d 0%, #6789b9 100%);
    opacity: 0;
    position: absolute;
    top: -8px;
    left: -8px;
    border-radius: 50%;
    z-index: 0;
    @include respond-to(992) {
      width: 75px;
      height: 75px;
      top: -6px;
      left: -6px; }
    @include respond-to(480) {
      width: 50px;
      height: 50px; } }
  &:hover {
    &:after {
      opacity: .22; } }
  i {
    font-size: 19px;
    color: $white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 1;
    opacity: .999;
    @include respond-to(992) {
      font-size: 34px; }
    @include respond-to(480) {
      font-size: 19px; } } }

.insta {
  @extend .vk;
  background-image: linear-gradient(45deg, #9c35a5 0%, #c53087 50%, #f87031 100%);
  position: relative;
  @include respond-to(992) {
    margin-left: 18px; }
  &:after {
    background-image: linear-gradient(45deg, #9c35a5 0%, #c53087 50%, #f87031 100%); }
  i {
    font-size: 19px;
    color: $white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 1;
    opacity: .999;
    @include respond-to(992) {
      font-size: 34px; }
    @include respond-to(480) {
      font-size: 19px; } } }

.rotate {
  transform: rotate(90deg);
  transition: .5s ease; }

.dark {
  font-family: $museoBold;
  color: $dark;
  display: inline; }

.green {
  display: inline;
  font-family: $museoBold;
  color: $green; }

.through {
  font-family: $museoMid;
  text-decoration: line-through;
  display: inline; }

.orange {
  color: $orange; }

.rub {
  font-family: $rub, bold;
  //text-transform: uppercase
  font-weight: 900; }

#layout {
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  position: fixed;
  background: $black;
  opacity: .8;
  z-index: 999; }

.fix-padding {
  padding-right: 17px;
  overflow: hidden; }

.humb {
  display: none;
  position: absolute;
  top: 0;
  right: 20px;
  width: 60px;
  height: 47px;
  cursor: pointer;
  @include respond-to(950) {
    display: inline-block; }
  &__line {
    width: $bar-width;
    height: $bar-height;
    position: relative;
    transform: translateY($bar-spacing);
    background: $orange;
    transition: all 0ms 300ms;
    &:before {
      content: "";
      width: $bar-width;
      height: $bar-height;
      position: absolute;
      left: 0;
      bottom: $bar-spacing;
      background: $orange;
      transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
    &:after {
      content: "";
      width: $bar-width;
      height: $bar-height;
      position: absolute;
      left: 0;
      top: $bar-spacing;
      background: $orange;
      transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); } } }

.animate {
  background: rgba(255, 255, 255, 0);
  &:after {
    top: 0;
    transform: rotate(45deg);
    transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); }
  &:before {
    bottom: 0;
    transform: rotate(-45deg);
    transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); } }

.desc {
  display: inline-block;
  @include respond-to(992) {
    display: none; } }

.mob {
  display: none;
  @include respond-to(992) {
    display: block; } }

.vk-button {
  width: 290px;
  height: 34px;
  display: flex;
  align-items: center;
  background-image: linear-gradient(13deg, #3d598d 0%, #6789b9 100%);
  border-radius: 5px;
  color: $white;
  padding-left: 15px;
  box-sizing: border-box;
  position: absolute;
  top: -79px;
  left: 50%;
  transform: translateX(-50%);
  opacity: .999;
  z-index: 100;
  @include respond-to(992) {
    top: 0; }
  @include respond-to(690) {
    top: 65px; }
  @include respond-to(690) {
    width: 70%;
    box-sizing: border-box; }
  @include respond-to(542) {
    width: 292px;
    top: 140px; }
  @include respond-to(462) {
    top: 200px; }
  &:hover {
    box-shadow: 0 0 5px 5px rgba(6,120,155,0.21); }
  i {
    font-size: 19px;
    @include respond-to(992) {
      font-size: 32px; }
    @include respond-to(542) {
      font-size: 19px; } }
  span {
    font-family: $museoMid;
    font-size: 14px;
    padding-left: 15px;
    @include respond-to(992) {
      font-size: 24px; }
    @include respond-to(542) {
      font-size: 14px;
      vertical-align: middle; } } }


.img-modal {
  //background: #000
  position: fixed;
  width: auto;
  height: 80%;
  left: 0;
  top: 0;
  right: 0;
  z-index: 100000;
  img {
    width: 100%;
    height: auto;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0; } }
