.addit {
  position: relative;
  &:before {
    content: ""; }
  &__ant {
    width: 29%;
    position: absolute;
    top: -305px;
    right: 0;
    &:after {
      content: "";
      width: 192px;
      height: 120px;
      background: url(../../../img/contur.png) no-repeat;
      position: absolute;
      right: 90px;
      bottom: -125px; }
    @include respond-to(1216) {
      display: none; } }
  &__ant-img {
    display: block;
    object-fit: cover; }
  &__ant-text {
    font-family: $epsilon;
    font-size: 35px;
    color: $dark; }
  &__wrapper {
    margin-top: 155px;
    @include respond-to(992) {
      flex-direction: column;
      justify-content: space-around;
      align-items: center; } }
  &__item {
    width: 48%;
    box-shadow: 0 6px 49px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    background-color: $white;
    position: relative;
    padding: 20px;
    box-sizing: border-box;
    margin-bottom: 120px;
    @include respond-to(1050) {
      margin-bottom: 110px;
      width: 60%; }
    @include respond-to(917) {
      width: 70%; }
    @include respond-to(793) {
      width: 80%; }
    @include respond-to(707) {
      width: 90%; }
    @include respond-to(635) {
      width: 95%; }
    @include respond-to(601) {
      width: 100%; } }
  &__info {
    margin-top: 20px; }
  &__title {
    font-family: $museoBold;
    font-size: 20px; }
  &__text {
    margin-top: 10px; }
  &__col {
    width: 47%;
    display: block;
    font-family: $museo;
    font-size: 16px;
    color: $grey;
    @include respond-to(480) {
      width: 100%;
      margin-bottom: 30px; } }
  &__list {
    position: relative;
    &:after {
      content: "70*70*70";
      font-family: $museoBold;
      font-size: 12px;
      position: absolute;
      top: 0;
      right: 0;
      @include respond-to(649) {
        top: -10px; } }
    li {
      padding-bottom: 9px;
      position: relative;
      &:before {
        content: "";
        width: 5px;
        height: 5px;
        background: $green;
        border-radius: 50%;
        position: absolute;
        top: 8px;
        left: -10px; } } }
  &__price {
    font-family: $museoBold;
    font-size: 20px;
    color: $dark;
    display: inline-block;
    margin-left: 35px;
    @include respond-to(480) {
      margin-left: 0;
      margin-top: 20px; } }
  &__txt {
    font-family: $museo;
    font-size: 16px;
    color: $grey;
    margin-bottom: 50px; }
  &__radio {
    display: inline-block;
    float: right;
    margin-top: 10px;
    @include respond-to(480) {
      margin-top: 20px;
      float: none; } }
  &__btn-block {
    @include respond-to(480) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; } } }


.fotorama {
  @include respond-to(570) {
    height: auto; }
  &__big {
    width: 217px;
    height: 153px;
    display: inline-block;
    border-radius: 5px;
    overflow: hidden;
    position: absolute;
    left: 20px;
    top: -77px;
    cursor: pointer;
    @include respond-to(570) {
      position: absolute;
      left: 50%;
      transform: translateX(-50%); }
    @include respond-to(480) {
      width: 85%;
      height: auto; }
    img {
      width: 100%;
      height: auto;
      object-fit: cover; } }
  &__thumbnails {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 50%;
    float: right;
    @include respond-to(570) {
      float: none;
      width: 75%;
      margin: 0 auto;
      justify-content: space-around;
      padding-top: 65px; }
    @include respond-to(480) {
      padding-top: 45%;
      width: 100%; }
    @include respond-to(423) {
      width: 100%; }
    a {
      display: inline-block;
      width: 79px;
      height: 56px;
      border-radius: 5px;
      overflow: hidden;
      cursor: pointer;
      @include respond-to(1167) {
        width: 67px;
        height: auto; }
      @include respond-to(1050) {
        width: 79px; }
      @include respond-to(480) {
        width: 27%; }
      @include respond-to(336) {
        width: 67px; }
      img {
        width: 100%;
        height: auto;
        object-fit: cover; } } } }

input.radio-btn {
  display: none; }
label.radio-label {
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 5px;
  border: 1px solid $grey3;
  font-family: $museoBold;
  font-size: 20px;
  color: $grey3;
  text-transform: uppercase;
  text-align: center;
  line-height: 30px;
  cursor: pointer; }

input.radio-btn:checked+label.radio-label {
  background-image: linear-gradient(-202deg, #ffad09 0%, #ff8609 100%);
  border: none;
  color: $white;
  width: 32px;
  height: 32px; }

.non_after {
  &:after {
    content: ''; } }
