.front {
  background-image: url(../img/top_bg.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-color: $white;
  &__wrapper {
    padding-bottom: 63px;
    @include respond-to(992) {
      display: flex;
      flex-direction: column-reverse;
      width: 100%; } }
  &__header {
    padding-top: 83px;
    float: left;
    z-index: 1;
    opacity: .999;
    @include respond-to(480) {
      padding-top: 42px; }
    span {
      font-family: $museoMid;
      font-size: 2.2em;
      color: $dark;
      display: block;
      margin-top: 36px;
      @include respond-to(992) {
        font-size: 4.6vw;
        text-align: center; } } }
  &__btn {
    margin-top: 56px;
    @include respond-to(992) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; } }
  &__social {
    display: inline-block;
    transform: translateY(10px);
    @include respond-to(992) {
      margin-top: 50px; }
    @include respond-to(480) {
      margin-top: 25px; } }
  &__img {
    width: 62.5%;
    float: right;
    margin-top: -355px;
    z-index: 0;
    @include respond-to(1068) {
      margin-top: -330px; }
    @include respond-to(992) {
      float: none;
      width: 100%;
      margin-top: 0; }
    @include respond-to(480) {
      margin-top: -150px; }
    img {
      width: 100%;
      object-fit: cover; } }
  &__arr {
    position: absolute;
    left: 165px;
    bottom: 120px;
    width: 31.2%;
    height: auto;
    @include respond-to(1124) {
      width: 22.2%; }
    @include respond-to(1041) {
      bottom: 80px; }
    @include respond-to(992) {
      display: none; }
    img {
      width: 100%;
      object-fit: cover; } } }


