.question {
  padding-top: 31px;
  &__wrapper {
    margin-top: 64px;
    li {
      width: 75%;
      margin-bottom: 43px;
      @include respond-to(992) {
        width: 100%; } } }
  &__title {
    font-family: $museoMid;
    font-size: 22px;
    color: $green;
    position: relative;
    @include respond-to(690) {
      font-size: 35px;
      padding-left: 50px; }
    @include respond-to(480) {
      font-size: 25px; }
    &:before {
      content: "";
      width: 25px;
      height: 25px;
      background: url(../img/qwn.png) no-repeat;
      position: absolute;
      top: 0;
      left: -35px;
      @include respond-to(690) {
        top: 4px;
        left: 0;
        background: url(../img/qwn_mob.png) no-repeat;
        width: 35px;
        height: 35px; } } }
  &__text {
    font-family: $museo;
    font-size: 16px;
    color: $grey;
    padding-top: 19px;
    @include respond-to(690) {
      font-size: 25px;
      padding-left: 50px; }
    @include respond-to(480) {
      font-size: 20px; } }
  &__btn-block {
    width: 100%;
    position: relative; }
  &__btn {
    position: relative;
    left: 50%;
    transform: translateX(-50%); }
  &__btn-lebel {
    font-family: $epsilon;
    font-size: 35px;
    color: $dark;
    position: absolute;
    right: 0;
    top: -143px;
    @include respond-to(992) {
      display: none; }
    &:after {
      content: "";
      width: 441px;
      height: 135px;
      background: url(../../../img/arrow__qtn.png) no-repeat;
      position: absolute;
      left: -135px;
      bottom: -102px; } } }
